@import url(../molecules/article.css);
@import url(../molecules/partners-list.css);

.about {
	color: var(--color-light-primary);
}

.about__body {
	position: relative;
}

@media (width >= 728px) {
	.about__body {
		--about-body-grid-columns-count: 3;
		--about-body-column-gap: 1rem;

		display: grid;
		grid-template-columns: repeat(var(--about-body-grid-columns-count), 1fr);
		column-gap: var(--about-body-column-gap);
		position: relative;
	}
}

@media (width >= 728px) {
	.about__body:after {
		--about-body-after: 0.125rem;

		content: '';
		height: 100%;
		width: var(--about-body-after);
		background-color: currentColor;
		border-radius: var(--about-body-after);
		position: absolute;
		top: 0;
		right: calc(100% / var(--about-body-grid-columns-count) - var(--about-body-column-gap) / 2 + var(--about-body-after) * 2);
	}
}

.about__article {
	grid-column: 1 / 3;
}

.about__partners {
	grid-column: 3 / -1;
}

@media (width < 728px) {
	.about__partners {
		display: none;
	}
}
