.partners-list {
	margin: 0;
	padding: 0;
	list-style: none;
}

@media (width < 728px) {
	.partners-list {
		display: grid;
		grid-template-columns: 1fr 1fr;
	}
}

@media (width >= 728px) {
	.partners-list {
		display: flex;
		justify-content: space-evenly;
	}
}

.partners-list--vertical {
	flex-direction: column;
}

.partners-list__item {
	display: flex;
	align-items: center;
	justify-content: center;
}

@media (width < 728px) {
	.partners-list__img {
		width: 6rem;
	}
}

@media (728px <= width < 1024px) {
	.partners-list__img {
		width: 10rem;
	}
}

@media (1024px <= width < 1440px) {
	.partners-list__img {
		width: 12rem;
	}
}

@media (width >= 1440px) {
	.partners-list__img {
		width: 14rem;
	}
}
